import { shop } from "projectSettings"
import React, { useState, useEffect } from "react"
import { window, document } from "browser-monads"

export function getKebabFromCamelCase(str) {
  let arr = str.split("-")
  let capital = arr.map((item, index) =>
    index
      ? item.charAt(0).toUpperCase() + item.slice(1).toLowerCase()
      : item.toLowerCase()
  )
  let capitalString = capital.join("")
  return capitalString
}

export function getNodeCategoryName(category) {
  const categoryUrl = category
  const categoryWords = categoryUrl.split("-")
  const lowerCaseWord = categoryWords[0]
  const upperCaseWords = categoryWords
    .map(element => {
      element = element
        .split(/\s+/)
        .map(word => word[0].toUpperCase() + word.substring(1))
        .join(" ")
      return element
    })
    .slice(1)
  let categoryName = lowerCaseWord.concat(upperCaseWords)
  return categoryName
}

export function getCategoryInfo(category) {
  const categoryName = getKebabFromCamelCase(category)
  return shop.categories[`${categoryName}`]
}

export function useWindowWidth() {
  const isBrowser = typeof window !== "undefined"
  const [width, setWidth] = useState(isBrowser ? window.innerWidth : 0)
  useEffect(() => {
    if (!isBrowser) return false
    const handleResize = () => setWidth(window.innerWidth)
    window.addEventListener("resize", handleResize)
    return () => {
      window.removeEventListener("resize", handleResize)
    }
  })
  return width
}

export function printPage() {
  var print_div = document.getElementById("content-for-print")
  var print_area = window.open()
  // print_area.document.write(
  //   '<style media="print">' + ".no-print {display: none !important;}</style>"
  // )
  print_area.document.write(print_div.innerHTML)
  print_area.document.close()
  print_area.focus()
  print_area.print()
  print_area.close()
}

export function setToLS(key, value) {
  window.localStorage.setItem(key, JSON.stringify(value))
}

export function getFromLS(key) {
  const value = window.localStorage.getItem(key)
  if (value) {
    return value
  }
}

export function calcExperience() {
  let num = 1992
  let now = new Date().getFullYear()
  let res = now - num - 16
  return res
}

export function useThemeDetector() {
  const getCurrentTheme = () =>
    window.matchMedia("(prefers-color-scheme: dark)").matches
  const [isDarkTheme, setIsDarkTheme] = useState(getCurrentTheme())
  const mqListener = e => {
    setIsDarkTheme(e.matches)
  }

  useEffect(() => {
    const darkThemeMq = window.matchMedia("(prefers-color-scheme: dark)")
    darkThemeMq.addListener(mqListener)
    return () => darkThemeMq.removeListener(mqListener)
  }, [])
  return isDarkTheme
}
